*{
    margin: 0;
    padding: 0;
}
#home{
    height: 100vh;
    width: 100vw;
    background-image:linear-gradient(to bottom, rgba(245, 246, 252, 0.52), rgba(255, 155, 197, 0.73)),
    url('../../assets/img/jason-strull-KQ0C6WtEGlo-unsplash.jpg');
      
     justify-content: space-around;
     display: flex;
     background-size: cover;
     background-repeat: no-repeat;
     background-position: 50% 50%;
    section{
        background-color:rgba(227,226,223,0.7);
        padding-left: $md;
        padding-right: $lg;
        height: 80%;
        width: 35%;


    }
    .bulb{
        color: rgb(241, 231, 86);
        font-size: 6rem;
        transition: 0.3s linear all;
        
    }
    .bulb:hover{
        font-size: $xxlg;
    }
    h1{
        text-align: center;
        color: #9A1750;
        font-weight: 5rem;
        font-size: 80px;
        font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    }
    .play-button-container{
        width: 100%;
        margin-top: 4rem;
        
    }
    a.play-button{
        
        background-color:$pink;
        display: block;
        justify-content: center;
        text-align: center;
        padding: 4px;
        text-decoration: none;
        color :black;
        border-radius: $border-radius;
        cursor: pointer;
        font-size: 1.4rem;
        transition: 0.3s linear all;


    }
    a.play-button:hover{
        background-color: rgb(90, 87, 87);
        color: $white;
    }
    .auth-button-container{
        width: 100%;
        display: flex;
        justify-content: space-between;
        margin-top: $md;
        

    }
    a.auth-buttons{
        background-color:#EE4C7C;
        color: black;
        text-align: center;
        font-size: $sm;
        padding: 4px;
        width:  45%;
        border-radius: $border-radius;
        transition: 0.3s linear all;
    }
    a.auth-buttons:hover{
        background-color: rgb(90, 87, 87);
        
        color:$white;
    }
    // #login{
    //     background-color: rgb(35, 169, 247);
    // }
    // #register{
    //     background-color: rgb(228, 34, 0);
    // }
    

     
}  
@media (max-width:768px){
    #home section{
        padding: 0 rem;
        width: 100%;
        height: 100%;
    }
}