.questions{
    background-color: $grey;
    border-left: 5px solid #9A1750;
    width: 70%;
    margin: 4rem auto;
    padding: $md;
}
.lifeline{
    display: flex;
    justify-content: space-between;

}
.life{
     position: relative;
     top:-4px;
    
}
.lifeline-icon{
    color: #9A1750;
}
.lifeline-icon:hover{
    color: $orange;
}
h5{
    font-size: 1.2rem;
    margin-bottom: $lg;
    line-height: 1.5;
    text-align: center;
}
.options{
    display: inline-block;
    width: 50%;

}
.option{
    background-color:$pink;
    border-radius: $border-radius;
    text-align: center;
    padding: $normal;
    margin: $md auto;
    transition: 0.3s linear all;
    cursor: pointer;
    color:$white;
    width: 90%;
}

.option:hover{
    background-color: #9A1750;
}
.buttons{
    display: flex;
    justify-content: flex-start;
    margin: 0 auto $sm auto;
    padding: $md;
    cursor: pointer;
    width: 80%;
}
.buttons button{
    border:none;
    margin-right:$sm;
    color:$white;
    padding:$xs $sm;
}
.buttons button:first-child{
    background-color: $orange;
    box-shadow:$shadow;
    transition: 0.2s linear all;
}
.buttons button:first-child:hover{
    background-color: darken($orange,10%);
}
.buttons button:nth-child(2){
    background-color: $lightgreen;
    box-shadow:$shadow;
    transition: 0.2s linear all;
}
.buttons button:nth-child(2):hover{
    background-color: darken($lightgreen,10%);
}
.buttons button:last-child{
    background-color:$tomato ;
    box-shadow:$shadow;
    transition: 0.2s linear all;
}
.buttons button:last-child:hover{
    background-color: darken($tomato,10%);
}
.toast-valid{
    background-color: green !important;
    border-radius: 10px;
}
.toast-invalid{
    background-color:rgb(197, 26, 26) !important;
    border-radius: 10px;
}
.disable{
    background-color: #ccc !important;
    pointer-events: none !important;
    box-shadow: none !important;
}